:root {
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

body {
    margin: 0;
}

nav {
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    height: 70px;
    display: flex;
    align-items: center;
    padding: 0px 30px;
}

h1 {
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 0px;
}

h3 {
    width: 320px;
    font-size: 16px;
    font-weight: 300;
}

p {
    font-size: 12px;
    margin-top: 9px;
    margin-bottom: 0px;
}


.hero--photo_grid {
    width: 600px;
    margin: auto;
    display: flex;
    margin-top: 21px;
}

.hero--paragraph {
    width: 600px;
    margin: auto;
}

.photo-grid {
    width: 100%;
}

.card {
    width: 176px;
    font-size: 12px;
    padding: 20px;
    position: relative;
}

.card--photo {
    border-radius: 9px;
    width: 100%;
}

.location {
    color: #918E9B;
}

.rating {
    color: #222222;
    padding-left: 2px;
}

.card--price {
    margin-top: auto;
}

.cards-list {
    width: 600px;
    margin: auto;
    overflow: auto;
    white-space: nowrap;
}

.cards-list>.card {
    display: inline-block;
}

.card--badge {
    position: absolute;
    top: 25px;
    left: 25px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}